import appTabNav from "../../../components/basic-component/app-tab-nav/app-tab-nav.vue";
import uMask from '../../../components/basic-component/u-mask/u-mask.vue';
import parse from "../../../components/basic-component/app-rich/parse.vue";
import appNoGoods from '../../../components/page-component/app-no-goods/app-no-goods.vue';
import { mapState, mapGetters } from "vuex";

export default {
    name: "index",
    data() {
        return {
            currentRoute: this.$platDiff.route(),
            tabbarbool: false,
            iphone_x: false,
            tabList: [
                {id:0, name: '兑换礼品'},
                {id:1, name: '兑换记录'}
            ],
            over: false,
            hiddenBottom: true,
            code: '',
            bottom: '-998rpx',
            activeTab: '0',
            list: [],
            setting: {},
            animationData: {},
            showRules: false,
            dialog: false,
            more: false,
            loading: false,
            is_not_wechat: false,
            msg: '',
            other: '',
            page: 1,
        };
    },
    watch: {
        tabBarNavs: {
            handler: function() {
                this.b();
            },
            immediate: true,
        }
    },
    components: {
        "app-tab-nav": appTabNav,
        parse,
        "app-no-goods": appNoGoods,
        "u-mask": uMask
    },
    computed: {
        ...mapState({
            tabBarNavs: state => state.mallConfig.navbar.navs,
        }),
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme'
        })
    },

    onShareAppMessage() {
        return this.$shareAppMessage({
            title: this.$children[0].navigationBarTitle,
            path: "/plugins/exchange/index/index",
        });
    },


    onShareTimeline() {
      // 分享朋友圈beta
      return this.$shareTimeline({
        title: this.$children[0].navigationBarTitle,
        query: {} // 此处填写页面的参数
      });
    },

    onReachBottom() {
        if(this.more) {
            this.page++;
            this.getMore();
        }
    },
    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;



        if(options.code) {
            that.code = options.code
        }
        if(options.token && options.library_id && options.created_at) {
            that.getActive(options.token, options.library_id, options.created_at)
        }

        wx.showShareMenu({
          menus: ['shareAppMessage', 'shareTimeline']
        })

        uni.getSystemInfo({
            success: function (res) {
                that.height = -res.screenHeight;
                if(res.model.indexOf('iPhone X') > -1 || res.model.indexOf('iPhone 11') > -1 || res.model.indexOf('iPhone11') > -1 || res.model.indexOf('iPhone12') > -1 || res.model.indexOf('Unknown Device') > -1) {
                    that.iphone_x = true;
                }
            }
        });
        that.$request({
            url: that.$api.exchange.setting,
        }).then(response=>{
            if(response.code == 0) {
                that.setting = response.data;
            }else {
                uni.showToast({
                    title: response.msg,
                    icon: 'none',
                    duration: 1000
                });
            }
        })
    },
    onShow() {
        if(this.activeTab == '1') {
            this.getList();
        }
    },
    methods: {
        getActive(token, library_id, created_at) {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '获取兑换码...'
            });
            that.$request({
                url: that.$api.exchange.code,
                data: {
                    token: token,
                    library_id: library_id,
                    created_at: created_at,
                }
            }).then(response=>{
                uni.hideLoading();
                this.loading = false;
                if(response.code == 0) {
                    this.code = response.data.code
                }else {
                    this.msg = response.msg;
                    this.other = '';
                    this.dialog = true;
                }
            }).catch(response => {
                uni.hideLoading();
            });
        },
        b() {

            let currentRoute = this.currentRoute;
            for (let i = 0; i < this.tabBarNavs.length; i++) {
                if(currentRoute.includes(this.tabBarNavs[i].url.split('?')[0])) {
                    return this.tabbarbool = true;
                }
            }

            return  this.tabbarbool = false;
        },
        showRule() {
            let that = this;
            that.over = false;
            that.showRules = true;
            var animation = uni.createAnimation({
                duration: 1000,
                timingFunction: 'ease',
            })
            that.animationData = animation
            setTimeout(function(){
                let num = 0;
                if(that.tabbarbool) {
                    num += 55
                }
                animation.bottom(-500).bottom(num).step()
                that.animationData = animation.export();
                setTimeout(function(){
                    that.bottom = num *2 + 'rpx';
                    that.over = true;
                },1200)
            },200)
        },
        scan() {
            let that = this;

            uni.scanCode({
                success(res) {
                    if(res.scanType == "CODE_128" || (res.result && res.result.length == 12)) {
                        that.code = res.result
                    } else {
                        if(res.path) {
                            uni.showLoading({
                                mask: true,
                                title: '获取兑换码...'
                            });
                            let path = res.path
                            path = path.split('?');
                            let scene = path[1];
                            let reg = new RegExp("scene=", "g");
                            scene = path[1].replace(reg, "");
                            that.$request({
                                url: that.$api.default.qrcode_parameter,
                                data: {
                                    token: scene
                                }
                            }).then(response => {
                                if (response.code === 0) {
                                    if(response.data.detail && response.data.detail.data.type == 'auto') {
                                        that.getActive(response.data.detail.data.token, response.data.detail.data.library_id, response.data.detail.data.created_at)
                                    }else {
                                        uni.hideLoading();
                                        uni.showToast({
                                            title: '未获取到兑换码',
                                            icon: 'none',
                                            duration: 1000
                                        });
                                        that.code = response.data.detail.data.code;
                                    }
                                }
                            });
                        }else {
                            uni.showToast({
                                title: '暂不支持此二维码，请使用应用内扫一扫功能',
                                icon: 'none',
                                duration: 1000
                            });
                        }
                    }
                }
            })







































































        },
        tabStatus(e) {
            if(this.loading) {
                return false
            }
            this.list = [];
            this.page = 1;
            this.activeTab = e.currentTarget.dataset.id;
            this.getList();
        },
        getMore() {
            let that = this;
            if(this.loading) {
                return false
            }
            this.loading = true;
            that.more = false;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that.$request({
                url: that.$api.exchange.log,
                data: {
                    page: that.page
                }
            }).then(response=>{
                uni.hideLoading();
                this.loading = false;
                if(response.code == 0) {
                    that.list = that.list.concat(response.data.list);
                    if(that.list.length == response.data.pagination.pageSize) {
                        that.more = true;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
            });
        },
        getList() {
            let that = this;
            if(this.loading) {
                return false
            }
            this.loading = true;
            that.page = 1;
            that.more = false;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that.$request({
                url: that.$api.exchange.log
            }).then(response=>{
                this.loading = false;
                uni.hideLoading();
                if(response.code == 0) {
                    that.list = response.data.list;
                    if(that.list.length == response.data.pagination.pageSize) {
                        that.more = true;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
            });
        },
        toDetail(item) {
            uni.navigateTo({
                url: '/plugins/exchange/detail/detail?code=' + item.code + '&exchange=1'
            })
        },
        toGift() {
            uni.navigateTo({
                url: '/plugins/exchange/list/list'
            })
        },
        clerk() {
            let that = this;
            if(this.loading || !this.code) {
                return false
            }
            uni.showLoading({
                mask: true,
                title: '兑换中...'
            });
            this.loading = true;
            let code = this.code;
            that.$request({
                url: that.$api.exchange.info,
                data: {
                    code: code
                }
            }).then(response=>{
                this.loading = false;
                uni.hideLoading();
                if(response.code == 0) {
                    this.code = '';
                    uni.navigateTo({
                        url: '/plugins/exchange/detail/detail?code=' + code
                    })
                }else {
                    this.msg = response.msg;
                    this.other = '';
                    if(response.msg == '该兑换码未到使用时间!') {
                        this.other = response.data.valid_start_time + '-' + response.data.valid_end_time + ' 可用'
                    }
                    this.dialog = true;
                }
            }).catch(response => {
                uni.hideLoading();
            });
        }
    }
}
